<template>
  <div>
    <div class="main">
      <img v-for="picUrl in picUrls" :src="picUrl" :key="picUrl" />
    </div>
    <div v-if="isButtonVisible" id="bbutton" class="bbutton" @click="scrollToTop">
      ↑
    </div>
    <button class="loadmore" @click="paperAdd(1)">加一页</button>
    <button class="loadmore" @click="paperAdd(10)">跳10页</button>
    <button class="loadmore" @click="paperAdd(50)">跳50页</button>
  </div>
</template>

<script>
import jQuery from 'jquery'
export default {
  data () {
    return {
      isButtonVisible: false,
      paper: 1,
      picUrls: []
    }
  },
  mounted () {
    this.getmore(this.paper)
    window.addEventListener('scroll', this.checkTop)
  },
  beforeDestroy () {
    window.removeEventListener('scroll', this.checkTop)
  },
  methods: {
    paperAdd (add) {
      this.paper += add
      this.getmore(this.paper)
    },
    getmore (page = 1) {
      return jQuery.ajax({
        url: 'https://ai.gmnsheng.cn/webh/web/imgs/pageIsWeb',
        method: 'POST',
        data: {
          title: '',
          pageNum: page,
          pageSize: 10,
          navigatePages: 0
        }
      }).then(response => {
        if (response.code !== 200) {
          return false
        }
        let list = response.data.list

        let pics = list.map(item => item.multiplefile)
        if (page === 1) {
          pics.splice(0, 2)
        }
        let pArr = []
        pics.forEach(value => {
          pArr.push(...value.split(',&~'))
        })
        this.picUrls = [...this.picUrls, ...pArr] // 更新图片URL数组
      }).catch(error => {
        console.error('Error:', error)
        return false
      })
    },
    checkTop () {
      const scrollTop = document.documentElement.scrollTop || document.body.scrollTop
      const windowHeight = document.documentElement.clientHeight
      const scrollHeight = document.documentElement.scrollHeight
      this.isButtonVisible = scrollTop > 0
      if (scrollTop + windowHeight + 5 >= scrollHeight) {
        this.paperAdd(1)
      }
    },
    scrollToTop () {
      window.scrollTo({ top: 0, behavior: 'smooth' })
    }
  }
}
</script>

<style>
/* CSS 样式 */
.main {
  display: flex;
  flex-direction: column;
}
img {
  width: 100%;
}
.bbutton {
  position: fixed;
  bottom: 20px;
  right: 20px;
  border: 1px solid rgb(120, 120, 120);
  border-radius: 6px;
  padding: 5px;
  background-color: rgb(170, 170, 170);
}
</style>



